/* Popup.css */

.victory-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .victory-popup-content {
    background: white;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
    font-family: "CooperBlack";
    font-size: 70px;
  }
  
  @font-face {
    font-family: 'CooperBlack';
    src: url('~/public/fonts/COOPBL.TTF') format('truetype');
    font-weight: normal;
    font-style: normal;
  }